import React from "react";
import { makeStyles, useTheme } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  svgHover: {
    width: "100%",
    height: "100%",
    transition: "all 0.5s ease",
    "&:hover": {
      filter: theme.palette.type === "light" ? "invert(1) brightness(1.2) drop-shadow(0px 0px 1px rgba(0, 0, 0, 0.5))" : "brightness(1.2)",
    },
    display: "block",
    position: "relative",
    zIndex: 5,
  },
  lightThemeLogo: {
    filter: "invert(1) drop-shadow(0px 0px 1px rgba(0, 0, 0, 0.5))",
  },
  darkThemeLogo: {
    filter: "none",
  }
}));

export const Logo = () => { // Убедитесь, что экспорт именованный: export const Logo
  const classes = useStyles();
  const theme = useTheme();
  const isLight = theme.palette.type === "light";
  
  const logoSrc = `${process.env.PUBLIC_URL}/vz-logo-dark.svg?v=${Date.now()}`;

  return (
    <img
      src={logoSrc}
      alt="Logo"
      className={`${classes.svgHover} ${isLight ? classes.lightThemeLogo : classes.darkThemeLogo}`}
      style={{
        maxWidth: "100%",
        maxHeight: "100%",
      }}
      onError={(e) => {
        console.log("Ошибка загрузки логотипа:", e);
        e.target.onerror = null;
      }}
    />
  );
};

