import React, { useEffect, useRef, useState, useContext } from 'react';
import { ThemeContext } from "../theme/ThemeProvider";
import './CustomCursor.css';

const CustomCursor = () => {
  const [hidden, setHidden] = useState(false);
  const [clicked, setClicked] = useState(false);
  const [linkHovered, setLinkHovered] = useState(false);
  const { theme } = useContext(ThemeContext);

  // Для плавного движения
  const cursorRef = useRef(null);
  const followerRef = useRef(null);
  const mouse = useRef({ x: 0, y: 0 });
  const pos = useRef({ x: 0, y: 0 });
  const followerPos = useRef({ x: 0, y: 0 });

  useEffect(() => {
    const move = (e) => {
      mouse.current = { x: e.clientX, y: e.clientY };
    };
    const mDown = () => setClicked(true);
    const mUp = () => setClicked(false);
    const mLeave = () => setHidden(true);
    const mEnter = () => setHidden(false);

    window.addEventListener('mousemove', move);
    window.addEventListener('mousedown', mDown);
    window.addEventListener('mouseup', mUp);
    window.addEventListener('mouseleave', mLeave);
    window.addEventListener('mouseenter', mEnter);

    // Hover для интерактивных элементов
    const handleLinkHoverEvents = () => {
      document.querySelectorAll('a, button, [role="button"], input[type="submit"], input[type="button"]').forEach(el => {
        el.addEventListener('mouseenter', () => setLinkHovered(true));
        el.addEventListener('mouseleave', () => setLinkHovered(false));
      });
    };
    handleLinkHoverEvents();

    // Плавная анимация движения курсора
    let raf;
    const animate = () => {
      pos.current.x += (mouse.current.x - pos.current.x) * 0.25;
      pos.current.y += (mouse.current.y - pos.current.y) * 0.25;
      followerPos.current.x += (mouse.current.x - followerPos.current.x) * 0.12;
      followerPos.current.y += (mouse.current.y - followerPos.current.y) * 0.12;
      if (cursorRef.current) {
        cursorRef.current.style.left = `${pos.current.x}px`;
        cursorRef.current.style.top = `${pos.current.y}px`;
      }
      if (followerRef.current) {
        followerRef.current.style.left = `${followerPos.current.x}px`;
        followerRef.current.style.top = `${followerPos.current.y}px`;
      }
      raf = requestAnimationFrame(animate);
    };
    animate();

    return () => {
      window.removeEventListener('mousemove', move);
      window.removeEventListener('mousedown', mDown);
      window.removeEventListener('mouseup', mUp);
      window.removeEventListener('mouseleave', mLeave);
      window.removeEventListener('mouseenter', mEnter);
      cancelAnimationFrame(raf);
    };
  }, []);

  const cursorClasses = `cursor cursor--${theme}${hidden ? ' cursor--hidden' : ''}${clicked ? ' cursor--clicked' : ''}${linkHovered ? ' cursor--hover' : ''}`;
  const followerClasses = `cursor-follower cursor--${theme}${hidden ? ' cursor--hidden' : ''}${clicked ? ' cursor-follower--clicked' : ''}${linkHovered ? ' cursor-follower--hover' : ''}`;

  return (
    <>
      <div ref={cursorRef} className={cursorClasses} />
      <div ref={followerRef} className={followerClasses} />
    </>
  );
};

export default CustomCursor;
