import React, { lazy, Suspense, useEffect } from "react"; // Добавлен useEffect
import { HashRouter as Router, Switch, Route, useLocation } from "react-router-dom"; // Добавлен useLocation
import { HelmetMeta } from "./HelmetMeta";
import { ThemeProvider } from "../components/theme/ThemeProvider";
import { CssBaseline } from "@material-ui/core";
import { logCredits } from "../utils/logCredits";
import { Home } from "../pages/Home";
import CustomCursor from "../components/cursor/CustomCursor";

const Resume = lazy(() => import("../pages/Resume"));
const PageNotFound = lazy(() => import("../pages/PageNotFound"));

// Компонент для скролла наверх при смене роута или начальной загрузке
function ScrollToTop() {
  const { pathname } = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname]); // Срабатывает при смене pathname (роута)

  return null;
}

export const App = () => {
    logCredits();

    useEffect(() => {
      // Принудительный скролл в начало при самой первой загрузке приложения
      if (window.history.scrollRestoration) {
        window.history.scrollRestoration = 'manual';
      }
      window.scrollTo(0, 0);
    }, []);

    return (
      <ThemeProvider>
        <CssBaseline />
        <CustomCursor />
        <Router>
          <ScrollToTop /> {/* Добавляем компонент для скролла */}
          <HelmetMeta />
          <Switch>
              <Route path="/" exact component={Home} />
              <Suspense fallback={<div>Loading...</div>}>
                <Route path="/resume" component={Resume} />
                <Route path="*" component={PageNotFound} />
              </Suspense>
          </Switch>
        </Router>
      </ThemeProvider>
    );
};